<template>
  <div class="account_box">
    <div class="account_nav">
      <div class="account_nav_btn" @click="goBack">
        <i class="el-icon-back"></i>
      </div>
      <div class="account_nav_title">{{$t('Create YAS Account')}}</div>
      <div class="account_nav_text">
        <router-link to="/">
          <i class="el-icon-s-home"></i>
        </router-link>
      </div>
    </div>
    <div class="account_content">
      <div class="account_item">
        <div class="account_item_top">
          <div>{{$t('Account')}}</div>
          <div>
            <span class="blue" style="margin-right: 8px;" @click="randomName">{{$t('Feeling Lucky')}}</span>
            <span class="blue" @click="doCopyAccount">{{$t('COPY')}}</span>
          </div>
        </div>
        <div class="account_item_middle input_error_box">
          <input type="text" v-model="account_name" :placeholder="$t('Please input your account')" @input="checkAccount">
          <div v-if="errorType == 1" class="error_icon" style="color: red;"><i class="el-icon-error"></i></div>
          <div v-if="errorType == 2" class="error_icon" style="color: green;"><i class="el-icon-success"></i></div>
          <div v-if="errorType == 3" class="error_icon"><i class="el-icon-loading"></i></div>
        </div>
        <div class="account_item_foot">{{$t('AccountText')}}</div>
      </div>
      <div class="account_item">
        <div class="account_item_top">
          <div>{{$t('PublicKey')}}</div>
          <div class="blue" @click="generateKeyPair">{{$t('Create New PublicKey')}}</div>
        </div>
        <div class="account_item_middle">
          <textarea class="textarea_key bk_f1fdf5" v-model="publicKey" :placeholder="$t('Please enter the public key')"></textarea>
        </div>
        <div class="account_item_foot">{{$t('PublicKeyMessage')}}</div>
      </div>
      <div class="account_item">
        <div class="account_item_top">
          <div>{{$t('PrivateKey')}}</div>
          <div v-if="privateKey != ''" class="blue" @click="doCopyKey">{{$t('COPY')}}</div>
        </div>
        <div class="account_item_middle">
          <textarea class="textarea_key bk_fbf2f2" v-model="privateKey"></textarea>
        </div>
        <div class="account_item_foot red">{{$t('Do not tell anyone')}}</div>
      </div>

      <div class="account_item">
        <div class="account_item_top">
          <div>{{$t('Red Envelope Code')}}</div>
        </div>
        <div class="account_item_middle">
          <textarea class="textarea_redp bk_fffcdd" v-model="redStr"></textarea>
        </div>
      </div>

      <div class="account_text">
        <p class="account_text_title red">{{$t('Save offline')}}</p>
        <p>{{$t('Save offline Content')}}</p>
      </div>
      <div class="account_text">
        <p class="account_text_title red">{{$t('Please do not use network transmission')}}</p>
        <p>{{$t('Please do not use network transmission Content')}}</p>
      </div>
      <div class="account_text">
        <p class="account_text_title">{{$t('Tips')}}</p>
        <p>{{$t('Tips Content')}}</p>
      </div>
      <div class="account_text">
        <p class="account_text_title">{{$t('Usage')}}</p>
        <p>{{$t('Usage Content')}}</p>
      </div>
      <div class="account_item">
        <button @click="newAccount">{{$t('Create Account')}}</button>
      </div>
    </div>
    <loading :loading-status="loading"></loading>
    <message :message-status="messageStatus" :message-text="messageText"></message>
  </div>
</template>

<script>
export default {
  components: {
    loading: resolve => {
      require(["@/components/loading.vue"], resolve);
    },
    message: resolve => {
      require(["@/components/message.vue"], resolve);
    }
  },
  data() {
    return {
      wxUrl: globalConfig.wxUrl,
      loading: false,
      messageStatus: false,
      messageText: "",
      account_name: "",
      publicKey: "",
      privateKey: "",
      redStr: "",
      redObj: null,
      errorType: 0 // 默认为0 1 代表错误 2代表正确 3 代表加载中
    };
  },
  created() {
    if (this.$route.query.id) {
      this.id = this.$route.query.id;
      this.f = this.$route.query.f;
      this.l = this.$route.query.l;
      this.redStr =
        this.$t("YAS Mainnet Red Envelope") +
        "\n - - - - - - - - - - - - -\n" +
        "恭喜发财，大吉大利" +
        "\n- - - - - - - - - - - - -\n" +
        this.$t("RedInfo1") +
        this.wxUrl +
        "/receive?id=" +
        this.id +
        "&f=" +
        this.f +
        "&l=" +
        this.l +
        "&lang=" +
        localStorage.getItem("Lang") +
        "\n" +
        this.$t("RedInfo2") +
        "\n" +
        this.$t("RedInfo3");
    }
    // await this.randomName();
    // await this.generateKeyPair();
    //await this.sendTransaction();
    //console.log(await this.chainApi.checkAccountExist(this.account_name));
    //console.log(await this.chainApi.checkAccountExist("qsx.io"));
  },

  methods: {
    checkAccount() {
      let REG = /^([a-z1-5]){12}$/;
      if (this.account_name.length != 12 || !REG.test(this.account_name)) {
        this.errorType = 1;
      } else {
        this.errorType = 3;
        this.chainApi.checkAccountExist(this.account_name).then(res => {
          if (res) {
            this.$message.error("账号已存在");
            this.errorType = 1;
          } else {
            this.errorType = 2;
          }
        });
      }
    },
    doCopyAccount() {
      let _this = this;
      _this.$copyText(_this.account_name).then(
        function(e) {
          _this.$message.success("复制成功");
        },
        function(e) {
          _this.$message.error("复制失败");
        }
      );
    },
    doCopyKey() {
      let _this = this;
      _this.$copyText(_this.privateKey).then(
        function(e) {
          _this.$message.success("复制成功");
        },
        function(e) {
          _this.$message.error("复制失败");
        }
      );
    },
    goBack() {
      window.history.go(-1);
    },
    newAccount() {
      let REG = /^([a-z1-5]){12}$/;
      if (this.account_name == "") {
        this.$message.error(this.$t("AccountNull"));
      } else if (!REG.test(this.account_name)) {
        this.$message.error(this.$t("AccountError"));
      } else if (this.publicKey == "") {
        this.$message.error(this.$t("PublicKeyNull"));
      // } else if (this.privateKey == "") {
      //   this.$message.error(this.$t("PrivateKeyNull"));
      } else if (this.redStr == "") {
        this.$message.error(this.$t("RedStrNull"));
      } else {
        this.sendTransaction();
      }
    },
    async sendTransaction() {
      //解析红包串 获得 id
      // let id = 1543977664355;

      // //get f + l from text
      // let f = "e1c10d7b18cbb2c3cabf3fd8f42c9f91";
      // let l = "26483373b58f36f418fe06f426d3c7ca";

      let id = this.redStr.split("=")[1].split("&")[0];
      let f = this.redStr.split("=")[2].split("&")[0];
      let l = this.redStr.split("=")[3].split("&")[0];
      console.log("===", id, f, l);

      //get info from chain
      try {
        this.redObj = await this.chainApi.getRecord(id);
        if (this.redObj.rest_number == 0) {
          this.$message.error("红包已领完");
        }
      } catch (err) {
        this.$message.error("红包不存在");
      }

      let time = this.redObj.create_time;
      let account = this.account_name;

      let sig = this.chainApi.sign(id, account, time, f, l);
      this.loading = true;
      try {
        let res = await this.chainApi.newAccount(
          id,
          account,
          sig,
          this.publicKey
        );
        this.loading = false;
        console.log(res);
        this.$message.success("交易成功");
      } catch (err) {
        this.loading = false;
        console.log(err);
        this.$message.error("交易失败");
      }
    },
    //生成公私钥
    async generateKeyPair() {
      this.loading = true;
      let pair = await this.chainApi.generateKeyPair();
      this.loading = false;
      this.privateKey = pair.privateKey;
      this.publicKey = pair.publicKey;
    },
    //生成随机账号
    randomName() {
      let validChar = [
        "1",
        "2",
        "3",
        "4",
        "5",
        "a",
        "b",
        "c",
        "d",
        "e",
        "f",
        "g",
        "h",
        "i",
        "j",
        "k",
        "l",
        "m",
        "n",
        "o",
        "p",
        "q",
        "r",
        "s",
        "t",
        "u",
        "v",
        "w",
        "x",
        "y",
        "z"
      ];
      let name = "";
      for (let i = 0; i < 12; i++) {
        let randomNum = Math.floor(Math.random() * 31);
        name += validChar[randomNum];
      }
      console.log(name);
      this.account_name = name;
      this.checkAccount();
    }
  },
  computed: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
@rem: round(1/75 * 2, 6) * 1rem;
.account_box {
  width: 100%;
  height: 100%;
  .account_nav {
    width: 100%;
    height: 44 * @rem;
    line-height: 44 * @rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15 * @rem;
    background: #ce2344;
    box-sizing: border-box;
    .account_nav_btn {
      width: 30 * @rem;
      height: 44 * @rem;
      display: flex;
      align-items: center;
      i {
        color: #fff;
        font-size: 25 * @rem;
      }
    }
    .account_nav_title {
      font-size: 12 * @rem;
      line-height: 44 * @rem;
      color: #fff;
    }
    .account_nav_text {
      a {
        width: 30 * @rem;
        height: 44 * @rem;
        display: flex;
        align-items: center;
        text-align: right;
        i {
          color: #fff;
          font-size: 23 * @rem;
        }
      }
    }
  }
  .account_content {
    padding: 0 16 * @rem;
    padding-bottom: 24 * @rem;
    box-sizing: border-box;

    .account_item {
      width: 100%;
      height: 100%;
      margin-top: 24 * @rem;
      box-sizing: border-box;
      .account_item_top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14 * @rem;
        color: #5d4220;
      }
      .account_item_middle {
        input {
          height: 28 * @rem;
          line-height: 28 * @rem;
          background-color: #f8f8f8;
          width: calc(100% - 20 * @rem);
          border: 0 none;
          padding: 10 * @rem;
          margin-top: 4 * @rem;
          word-break: break-all;
        }
        .textarea_key {
          width: calc(100% - 20 * @rem);
          border: 0 none;
          padding: 10 * @rem;
          margin-top: 4 * @rem;
          word-break: break-all;
          resize: none;
          font-size: 12 * @rem;
          height: 70 * @rem;
        }
        .textarea_redp {
          width: calc(100% - 22 * @rem);
          height: 170 * @rem;
          padding: 12 * @rem;
          border: 0 none;
          word-break: break-all;
          margin-top: 4 * @rem;
          resize: none;
        }
      }
      .input_error_box {
        position: relative;
        .error_icon {
          position: absolute;
          top: 4 * @rem;
          right: 0;
          height: 48 * @rem;
          width: 48 * @rem;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 16 * @rem;
        }
      }
      .account_item_foot {
        padding: 0 3 * @rem;
        color: #a69987;
        font-size: 12 * @rem;
        margin-top: 4 * @rem;
        text-align: left;
        box-sizing: border-box;
      }

      .bk_fbf2f2 {
        background: #fbf2f2;
      }
      .bk_f1fdf5 {
        background: #f1fdf5;
      }
      .bk_fffcdd {
        background: #fffcdd;
      }
      button {
        margin-bottom: 0;
        height: 50 * @rem;
        line-height: 50 * @rem;
        color: #5d4220;
        background-color: #fcdbb2;
        border-radius: 2 * @rem;
        width: 100%;
        font-size: 16 * @rem;
        text-align: center;
        border: none;
      }
    }
    .account_text {
      margin-top: 24 * @rem;
      text-align: left;
      color: #5d4220;
      font-size: 12 * @rem;
      line-height: 20 * @rem;
      .account_text_title {
        font-size: 14 * @rem;
      }
    }
  }
  .red {
    color: #ce2344 !important;
  }
  .blue {
    color: #288efb !important;
  }
}
</style>



